  grid-template-columns: 1fr auto auto;
  grid-template-areas: 'title image1 image2' 'description image1 image2';
}

.bookmark .bookmark-grid h2 {
  grid-area: title;
}

.bookmark .bookmark-grid img {
  max-width: 280px;
}

.bookmark .bookmark-grid #image1 {
  grid-area: image1;
}

.bookmark .bookmark-grid #image2 {
  grid-area: image2;
}

.bookmark .bookmark-grid .bookmark-description {
  grid-area: description;
}

@media (max-width: 767px) {
  .bookmark .bookmark-grid {
    grid-template-columns: 1fr 1fr;
    grid-template-areas: 'title title' 'image1 image2' 'description description';
  }
